import _ from 'lodash';
import types from './mutation-types';

const MAX_QUERIES_IN_HISTORY = 50;

function saveHistoryToLocalStorage(history) {
  const historyJson = JSON.stringify(history);

  localStorage.setItem('reindexerQueryHistory', historyJson);
}

function restoreHistoryFromLocalStorage() {
  const historyJson = localStorage.getItem('reindexerQueryHistory');
  const history = JSON.parse(historyJson);

  if (!_.isArray(history)) {
    return [];
  }

  return history;
}

export default {
  [types.SETTINGS_DATABASE_RESET](state) {
    state.database = '';
  },
  [types.SETTINGS_NAMESPACE_RESET](state) {
    state.namespace = '';
    state.namespacesSelectedFields = {};
  },
  [types.SET_DATABASE](state, payload) {
    const { name } = payload;

    state.database = name;
  },
  [types.SET_NAMESPACE](state, payload) {
    const { name } = payload;

    state.namespace = name;
  },
  [types.SIDEBAR_TOGGLE](state) {
    state.isSidebarOpened = !state.isSidebarOpened;
  },
  [types.TABLE_SET_NAMESPACES_COLUMNS](state, payload) {
    const { db, namespace, fields } = payload;

    if (_.isEmpty(state.namespacesSelectedFields[db])) {
      state.namespacesSelectedFields[db] = {};
    }

    state.namespacesSelectedFields[db][namespace] = fields;
  },
  [types.LOAD_QUERIES_FROM_HISTORY](state) {
    state.queryHistory = restoreHistoryFromLocalStorage();
  },
  [types.PUSH_QUERY_TO_HISTORY](state, payload) {
    const { query } = payload;
    const { queryHistory } = state;

    let history;

    if (_.size(queryHistory) < MAX_QUERIES_IN_HISTORY) {
      history = [
        query,
        ...queryHistory,
      ];
    } else {
      history = [
        query,
        ..._.slice(queryHistory, 0, MAX_QUERIES_IN_HISTORY - 1),
      ];
    }

    const uniqHistory = _.uniq(history);

    saveHistoryToLocalStorage(uniqHistory);

    state.queryHistory = uniqHistory;
  },
  [types.VIEW_ON_CHANGE](state, payload) {
    state.view = {
      ...payload,
    };
  },
};



