<template>
  <default-layout>
    <div slot="page">
      <preloader class="preloader" :isLoading='isLoading' />
      <b-alert show v-if="!db && !isLoading">Create a database</b-alert>
      <b-alert show v-if="db && !namespace">Select a namespace</b-alert>

      <b-container v-if="namespaceOneExists" fluid>
        <b-btn class="mb-3" size="sm" variant="success" @click="onAddIndexClick">Add index</b-btn>

        <b-modal v-model="indexModalCreateShow" title="Index create">
          <b-container fluid>
            <b-row class="mb-1">
              <b-col cols="2" class="col-sm-4 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.name' name="question" />name
              </b-col>
              <b-col>
                <b-input v-model="newIndex.name" />
              </b-col>
            </b-row>
            <b-row class="mb-1">
              <b-col cols="2" class="col-sm-4 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.jsonPath' name="question" />json_path
              </b-col>
              <b-col>
                <b-input v-model="newIndex.jsonPath" />
              </b-col>
            </b-row>
            <b-row class="mb-1">
              <b-col cols="2" class="col-sm-4 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.fieldType' name="question" />field_type
              </b-col>
              <b-col>
                <b-form-select id="field_type" :value="null" v-model="newIndex.fieldType"
                  :options="['int', 'int64', 'double', 'string', 'bool', 'composite']" />
              </b-col>
            </b-row>
            <b-row class="mb-1">
              <b-col cols="2" class="col-sm-4 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.indexType' name="question" />index_type
              </b-col>
              <b-col>
                <b-form-select id="index_type" :value="null" v-model="newIndex.indexType"
                  :options="['hash', 'tree', 'text', '-']" />
              </b-col>
            </b-row>
            <b-row class="mb-1">
              <b-col cols="2" class="col-sm-4 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.options' name="question" />options
              </b-col>
              <b-col class="text-center">
                <label class="mt-2"><input type="checkbox" v-model="newIndex.isPk">Pk</label>
                <label class="mt-2 ml-4"><input type="checkbox" v-model="newIndex.isArray">Array</label>
                <label class="mt-2 ml-4"><input type="checkbox" v-model="newIndex.isDense">Dense</label>
              </b-col>
            </b-row>
            <b-row class="mb-1">
              <b-col cols="2" class="col-sm-5 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.collateMode' name="question" />collate_mode
              </b-col>
              <b-col>
                <b-form-select :value="null" v-model="newIndex.collateMode"
                  :options="['none', 'ascii', 'utf8', 'numeric', 'custom']" />
              </b-col>
            </b-row>
            <b-row v-if="newIndex.collateMode == 'custom'" class="mb-1">
              <b-col cols="3" class="col-sm-5 mt-2">
                <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.sortOrderLetters' name="question" />sort_order_letters
              </b-col>
              <b-col>
                <b-input v-model="newIndex.sortOrderLetters" />
              </b-col>
            </b-row>
          </b-container>
          <div slot="modal-footer" class="w-100">
            <b-btn size="sm" class="float-right" variant="success" @click.prevent="onCreateIndexClick">
              Create
            </b-btn>
          </div>
        </b-modal>

        <table class="table table-sm table-bordered table-striped">
          <thead>
            <tr class="d-flex">
              <th class="col-3">Index</th>
              <th class="col-1">Field Type</th>
              <th class="col-1">Index Type</th>
              <th class="col-6">Options</th>
              <th class="col-1">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr class="d-flex" v-for="indexWithLabels, index in indexesWithLabels" :key="index">
              <td class="col-sm-3">{{indexWithLabels.name}}</td>
              <td class="col-sm-1">{{indexWithLabels.fieldType}}</td>
              <td class="col-sm-1">{{indexWithLabels.indexType}}</td>
              <td class="col-sm-6">
                <b-badge class="mr-4" v-for="option, index in indexWithLabels.options" :key="index" pill :variant="option.label">{{option.value}}</b-badge>
              </td>
              <td class="col-sm-1">
                <b-button
                  variant="danger"
                  size="sm"
                  class="mr-2"
                  @click.prevent="() => {onIndexDeleteQuestion({name: indexWithLabels.name})}"><octicon name="trashcan" /></b-button>
              </td>
            </tr>
          </tbody>
        </table>
      </b-container>

    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import DefaultLayout from '@/layouts/DefaultLayout';
import Octicon from 'vue-octicon/components/Octicon';
import Preloader from '@/components/Preloader';
import DataTable from '@/components/DataTable';

export default {
  name: 'NamespacesPage',

  components: {
    DefaultLayout,
    Octicon,
    Preloader,
    DataTable,
  },

  data() {
    const indexTemplate = {
      name: '',
      jsonPath: '',
      fieldType: '',
      indexType: '',
      collateMode: '',
      sortOrderLetters: '',
      isPk: false,
      isArray: false,
      isDense: false,
      isAppendable: false,
    };

    return {
      newIndex: {
        ...indexTemplate,
      },

      newIndexTemplate: {
        ...indexTemplate,
      },

      indexModalCreateShow: false,

      tooltips: {
        name: 'An index name is used in \'WHERE\' or \'JOIN\' queries.',
        jsonPath: `A name for mapping to json field. May contain nested fields: 'nested.field'.
May contain a few comma-separated json fileds: 'field1,field2' .`,
        fieldType: 'Index field type.',
        indexType: 'Index type.',
        options: `Pk - field is a part of a primary key. Struct must have at least 1 field tagged with pk;\n
Array - composite index;\n
Dense - reduce index size. For hash and tree it will save 8 bytes per unique key value.
For - it will save 4-8 bytes per each element. Useful for indexes with high selectivity, but for tree and hash indexes with low selectivity can seriously decrease update performance.`,
        collateMode: `numeric - create string index that provides values order in numeric sequence. The field type must be a string.\n
ascii - create case-insensitive string index works with ASCII. The field type must be a string.\n
utf8 - create case-insensitive string index works with UTF8. The field type must be a string.\n
custom  - create custom order string index.
`,
        sortOrderLetters: ' a sequence of letters which defines sort order',
      },
    };
  },

  computed: {
    ...mapState({
      db: state => state.settings.database,
      namespace: state => state.settings.namespace,
      namespaceOne: state => state.namespaces.one,
      isLoading: state => state.namespaces.isLoading,
    }),

    ...mapGetters({
      indexes: 'namespaces/indexes',
      fields: 'namespaces/indexFields',
    }),

    namespaceOneExists() {
      return !_.isEmpty(this.namespaceOne);
    },

    indexesSize() {
      return _.size(this.indexes);
    },

    selectedFields() {
      return _.cloneDeep(this.fields);
    },

    indexesWithLabels() {
      return this.indexes.map(idx => ({
        name: idx.name,
        fieldType: idx.field_type,
        indexType: idx.index_type,
        options: [{
          label: 'danger',
          value: idx.is_pk ? 'PK' : '',
        }, {
          label: 'success',
          value: idx.is_dense ? 'Dense' : '',
        }, {
          label: 'primary',
          value: idx.is_array ? 'Array' : '',
        }, {
          label: 'warning',
          value: !_.isEmpty(idx.collate_mode) && idx.collate_mode !== 'none' ? `Collate: ${idx.collate_mode}` : '',
        }, {
          label: 'secondary',
          value: !_.isEmpty(idx.sort_order_letters) ? `Sort order letters: "${idx.sort_order_letters}"` : '',
        }],
      }));
    },
  },

  methods: {
    onIndexCreate(index) {
      const pload = {
        db: this.db,
        namespace: this.namespace,
        index,
      };

      this.$store.dispatch('namespaces/createIndex', pload);
    },

    onIndexDeleteQuestion(index) {
      const { name } = index;

      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the index:',
        strongMessage: name,
        event: 'onIndexDelete',
        eventParams: name,
      });
    },

    onIndexAddQuestion(newIndex) {
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will create a new index:',
        strongMessage: newIndex,
        event: 'onIndexCreate',
        eventParams: newIndex,
      });
    },

    onAddIndexClick() {
      this.indexModalCreateShow = true;
    },

    onCreateIndexClick() {
      this.onIndexCreate(this.newIndex);

      this.indexModalCreateShow = false;
    },
  },

  watch: {
    namespaceOne(newNamespaceOne) {
      if (!_.isEqual(this.namespaceOne, newNamespaceOne)) {
        this.newIndex = _.copyDeep(this.newIndexTemplate);
      }
    },
  },
};
</script>

<style scoped>
.datatable {
  margin-top: 30px;
}

.icon_tooltip {
  position: relative;
  top: 3px;
  right: 3px;
}

.icon_tooltip:focus {
  outline: none;
  pointer-events: none;
}
</style>



