<template>
  <default-layout>
    <div slot="page">
      <preloader class="preloader" :isLoading='isLoading' />

      <b-container v-if="db" fluid class="mb-4">
          <b-row>
            <b-col cols="11">
              <b-form-textarea
                class="queryarea"
                v-model="customQuery"
                @input="changeQuery"
                placeholder="Query"
                :rows="3"
                :max-rows="6"
                :disabled="!db" />
            </b-col>
            <b-col cols="1">
              <b-button-group>
                <b-button :disabled="!db" @click.stop="onQueryExecute">Execute</b-button>
                <b-dropdown :disabled="!isAnyItemInQueryHistory" ref="queryselect">
                  <b-dropdown-item v-for="query, index in queryHistory" :key="index" @click.stop="onQuerySelect(query)">{{query}}</b-dropdown-item>
                </b-dropdown>
              </b-button-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col>
              <b-alert class="mt-3" v-if="!db && !isLoading" show>Create a database</b-alert>
              <b-alert class="mt-3" v-if="error" variant="danger" show>{{ error }}</b-alert>
            </b-col>
          </b-row>
          <b-row class="mt-4">
            <b-col>
              <b-button-group size="sm">
                <b-button variant="outline-primary" :pressed="view.isList" @click.prevent="onViewChange('list')">List</b-button>
                <b-button variant="outline-primary" :pressed="view.isTable" @click.prevent="onViewChange('table')">Table</b-button>
              </b-button-group>
            </b-col>
            <b-col>
              <center v-if="itemsSize">
                <strong>{{itemsSize}} item(s)</strong>
              </center>
            </b-col>
            <b-col></b-col>
          </b-row>
      </b-container>

      <b-container v-if="view.isList" fluid>
        <b-row v-for="item, index in items" :key="index" fluid>
          <b-col>
            <b-card>
              <tree-view :data="item" :options="{modifiable: false, maxDepth: 1, rootObjectKey: jsonRootName(item)}" />
            </b-card>
          </b-col>
        </b-row>
      </b-container>

      <b-container v-if="view.isTable" fluid>
        <b-row>
          <b-col>
            <data-table
              v-if="itemsSize"
              class="mt-1"
              :items="items"
              :columns="fields"
              :selectedFields="selectedFields"
            />
          </b-col>
        </b-row>
      </b-container>

    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import DefaultLayout from '@/layouts/DefaultLayout';
import DataTable from '@/components/DataTable';
import Preloader from '@/components/Preloader';


export default {
  name: 'ItemsPage',

  components: {
    DefaultLayout,
    DataTable,
    Preloader,
  },

  data() {
    return {
      customQuery: '',
      error: '',
    };
  },

  computed: {
    ...mapState({
      view: state => state.settings.view,
      queryHistory: state => state.settings.queryHistory,
      db: state => state.settings.database,
      items: state => state.items.queryItems,
      isLoading: state => state.items.isLoading,
    }),

    ...mapGetters({
      fields: 'items/queryFields',
    }),

    selectedFields() {
      return _.cloneDeep(this.fields);
    },

    itemsSize() {
      return _.size(this.items);
    },

    isAnyItemInQueryHistory() {
      return !_.isEmpty(this.queryHistory);
    },
  },

  methods: {
    jsonRootName(item) {
      if (_.isEmpty(item[this.fields.id])) {
        const firstField = this.fields[0];

        return `${firstField}_${item[firstField]}`;
      }

      return `${this.fields.id}_${item.id}`;
    },

    onViewChange(viewType) {
      if (viewType === 'table') {
        this.$store.dispatch('settings/viewOnChange', { isTable: true });

        return;
      }

      this.$store.dispatch('settings/viewOnChange', { isList: true });
    },

    onQueryExecute() {
      this.$refs.queryselect.visible = false;

      if (!this.db) {
        return;
      }

      if (_.isEmpty(this.customQuery)) {
        this.error = 'No query is written';

        return;
      }

      const query = this.customQuery.trim();

      const pload = {
        db: this.db,
        query,
      };

      this.$store.dispatch('settings/pushQueryToHistory', pload);

      // const upCasedQuery = this.customQuery.toUpperCase();
      // if (upCasedQuery.includes('SELECT') && !upCasedQuery.includes('LIMIT')) {
      //   this.error = 'Expect LIMIT expression for SELECT query';

      //   return;
      // }

      this.$store.dispatch('items/getItemsByQuery', pload);
    },

    onQuerySelect(query) {
      this.customQuery = query;

      this.onQueryExecute();
    },

    changeQuery() {
      this.error = '';
    },
  },

  mounted() {
    this.$store.dispatch('settings/loadQueriesFromHistory');
  },
};
</script>

<style scoped>
.editor {
  font-size: 20px;
  margin: 0 auto;
  border: 1px gray solid;
}
</style>



