<template>
  <default-layout>
    <div slot="page">
      <preloader :isLoading="isLoading" />

      <b-alert show v-if="!db && !isLoading">Create a database</b-alert>
      <b-alert show v-if="db && !namespace">Select a namespace</b-alert>

      <b-container v-if="db && namespace" fluid class="mb-4">
          <b-row>
              <b-col>
                <b-input-group size="sm">
                  <b-form-input placeholder="Filer" size="sm" />
                  <b-btn variant="success" size="sm">Find</b-btn>
                </b-input-group>
              </b-col>
          </b-row>
          <b-row class="mt-4">
            <b-col>
              <b-button-group size="sm">
                <b-button variant="outline-primary" :pressed="view.isList" @click.prevent="onViewChange('list')">List</b-button>
                <b-button variant="outline-primary" :pressed="view.isTable" @click.prevent="onViewChange('table')">Table</b-button>
                <b-button variant="success" :pressed="view.isEditor" @click.prevent="onViewChange('editor')">Editor</b-button>
              </b-button-group>
            </b-col>
            <b-col>
              <center v-if="itemsSize">
                <strong>{{totalItemsShowedStartInfo}}..{{totalItemsShowedFinishInfo}} of {{totalItemsInfo}}</strong>
              </center>
            </b-col>
            <b-col>
              <b-pagination size="sm" class="float-right ml-2" hide-ellipsis hide-goto-end-buttons :limit="0" :total-rows="totalItems"
                  :per-page="pageSize" :value="page" v-on:input="onPageChange">
              </b-pagination>

              <b-dropdown size="sm" variant="primary" class="float-right" :text="pageSizeString">
                <b-dropdown-item
                  v-for="pageSize in paginatorLimitOptions"
                  v-bind:key="pageSize"
                  @click="onPageSizeChange(pageSize)">{{pageSize}}
                </b-dropdown-item>
              </b-dropdown>
            </b-col>
          </b-row>
      </b-container>

      <b-container v-if="view.isList" fluid>
        <b-row v-for="item, index in items" :key="index">
          <b-col>
            <b-card>
              <tree-view :data="item" :options="{modifiable: false, maxDepth: 1, rootObjectKey: jsonRootName(item)}" />
            </b-card>
          </b-col>
        </b-row>
      </b-container>

      <b-container v-if="view.isTable" fluid>
        <b-row>
          <b-col>
            <data-table
              v-if="namespace"
              class="mt-1"
              isWithActions
              isUpdateAllowed
              isRemoveAllowed
              isCreateAllowed
              :title="namespace"
              :items="items"
              :itemTemplate="itemTemplate"
              :columns="columns"
              :selectedFields="columnsFromSettings"
              :sortableFields="sortableFields"
              :sortField="sortField"
              :sortDesc="sortDesc"
              :onSelectedFieldsChange="onSelectedFieldsChange"
              :onQuery="onQuery"
              :onItemDelete="onItemDeleteQuestion"
              :onItemUpdate="onItemUpdateQuestion"
            />
          </b-col>
        </b-row>
      </b-container>

      <b-container v-if="view.isEditor" fluid>
        <b-row>
          <b-col>
            <b-card>
              <editor ref="editor" class="editor" lang="json" theme="chrome" width="100%" :height="clHeight" :value="itemTemplate"  />
              <center>
                <b-btn @click="onEditorSave" class="mt-3" variant="success" size="md">Save</b-btn>
              </center>
            </b-card>
          </b-col>
        </b-row>
      </b-container>

    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import Octicon from 'vue-octicon/components/Octicon';
import Editor from 'vue2-ace-editor';
import DefaultLayout from '@/layouts/DefaultLayout';
import Modal from '@/components/Modal';
import Preloader from '@/components/Preloader';
import DataTable from '@/components/DataTable';


const EDITOR_HEIGHT_DEFAULT = 500;
const PAGINATOR_PAGES_OPTIONS = [10, 25, 50];

export default {
  name: 'ItemsPage',

  components: {
    DefaultLayout,
    Modal,
    Preloader,
    DataTable,
    Octicon,
    Editor,
  },

  created() {
    window.addEventListener('resize', this.resizeHandler);

    this.$nextTick(this.resizeHandler());
  },

  destroyed() {
    window.removeEventListener('resize', this.resizeHandler);
  },

  data() {
    return {
      jsonEditor: '',
      clHeight: EDITOR_HEIGHT_DEFAULT,
      paginatorPagesMax: PAGINATOR_PAGES_OPTIONS[0],
    };
  },

  computed: {
    ...mapState({
      view: state => state.settings.view,
      db: state => state.settings.database,
      namespace: state => state.settings.namespace,
      columnsSelected: state => state.settings.namespacesSelectedFields,
      items: state => state.items.list,
      totalItems: state => state.items.total,
      isLoading: state => state.items.isLoading,
    }),

    ...mapGetters({
      indexesFields: 'namespaces/indexesNames',
      sortableFields: 'namespaces/sortableFields',
      itemTemplate: 'namespaces/itemTemplate',
      columns: 'items/fields',
      sortField: 'items/sortField',
      sortDesc: 'items/sortDesc',
      pageSize: 'items/pageSize',
      page: 'items/page',
    }),

    itemsSize() {
      return _.size(this.items);
    },

    itemsJson() {
      return JSON.stringify(this.items);
    },

    columnsFromSettings() {
      const dbData = _.get(this.columnsSelected, this.db, null);

      if (!dbData) {
        return this.indexesFields;
      }

      return _.get(dbData, this.namespace, this.indexesFields);
    },

    paginatorLimitOptions() {
      return PAGINATOR_PAGES_OPTIONS;
    },

    pageSizeString() {
      return this.pageSize.toString();
    },

    totalItemsInfo() {
      return this.totalItems || this.itemsSize;
    },

    totalItemsShowedFinishInfo() {
      const totalPages = Math.ceil(this.totalItemsInfo / this.pageSize);

      return totalPages === this.page ? this.totalItemsInfo : this.page * this.pageSize;
    },

    totalItemsShowedStartInfo() {
      const start = (this.totalItemsShowedFinishInfo - this.pageSize) + 1;

      return start > 0 ? start : 1;
    },
  },

  methods: {
    jsonRootName(item) {
      if (!this.indexesFields[0]) {
        return `${this.namespace}`;
      }

      if (_.isEmpty(item)) {
        return `${this.namespace}_${this.indexesFields[0]}`;
      }

      if (_.isEmpty(this.indexesFields.id)) {
        return `${this.namespace}_${this.indexesFields[0]}_${item[this.indexesFields[0]]}`;
      }

      return `${this.namespace}_${this.indexesFields.id}_${item[this.indexesFields.id]}`;
    },

    onViewChange(viewType) {
      if (viewType === 'list') {
        this.$store.dispatch('settings/viewOnChange', { isList: true, isTable: false, isEditor: false });

        return;
      } else if (viewType === 'table') {
        this.$store.dispatch('settings/viewOnChange', { isList: false, isTable: true, isEditor: false });

        return;
      }

      this.$store.dispatch('settings/viewOnChange', { isList: false, isTable: false, isEditor: true });
    },

    onSelectedFieldsChange(fields) {
      this.$store.dispatch('settings/setSelectedFieldsForNamespace', { db: this.db, namespace: this.namespace, fields });
    },

    onQuery(params) {
      const pload = {
        db: this.db,
        namespace: encodeURIComponent(this.namespace),
      };
      this.$store.dispatch('items/setItemsParams', params).then(() => {
        this.$store.dispatch('items/getItemsList', pload);
      });
    },

    onItemDeleteQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the item:',
        strongMessage: itemPretty,
        event: 'onItemDelete',
        eventParams: itemPretty,
      });
    },

    onItemCreateQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will create a new item:',
        strongMessage: itemPretty,
        event: 'onItemCreate',
        eventParams: itemPretty,
      });
    },

    onItemUpdateQuestion(item) {
      const itemPretty = JSON.stringify(item, null, 2);
      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will change the existed item:',
        strongMessage: itemPretty,
        event: 'onItemUpdate',
        eventParams: itemPretty,
      });
    },

    resizeHandler() {
      this.clHeight = window.innerHeight > EDITOR_HEIGHT_DEFAULT
        ? window.innerHeight - 340 // TODO fixme. need to calc height through this.$el.offsetHeight
        : window.innerHeight;
    },

    onPageSizeChange(pageSize) {
      const params = {
        page: 1,
        pageSize,
      };

      this.onQuery(params);
    },

    onPageChange(page) {
      const { pageSize } = this;
      const params = {
        page,
        pageSize,
      };

      this.onQuery(params);
    },

    onEditorSave() {
      try {
        const item = JSON.parse(this.$refs.editor.contentBackup);
        this.onItemCreateQuestion(item);
      } catch (e) {
        _.noop();
      }
    },
  },
};
</script>

<style scoped>
.editor {
  font-size: 20px;
  margin: 0 auto;
  border: 1px gray solid;
}
</style>



